@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* font-family: 'Poppins', sans-serif; */
/* font-family: 'Playfair Display', serif; */

/* navbar css */
.navbar {
  font-family: "Poppins", sans-serif;
  font-size: 15px;
  line-height: 6px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px rgb(173, 142, 175);
  --webkit-box-shadow: 0 8px 6px -6px #f08b8b;
  border-bottom: salmon !important;
  background: linear-gradient(to right, lightpink, lightcoral);
  padding: 2px;
}

.nav-link {
  font-weight: 300 !important;
}

.active {
  border-bottom: 1px solid black;
}

.navbar-brand {
  font-weight: 700;
  font-family: "roboto", sans-serif;
  letter-spacing: 3px;
}

.logo-img {
  max-width: 70px;
  height: auto;
  margin-right: 10px;
  border-radius: 80%;
}

.container-fluid {
  color: black;
  padding: 2px;
  margin: 2px;
  font-size: 20px;
}

/* footer */
.footer {
  color: black;
  padding: 10px;
  background-color: lightcoral;
  background: linear-gradient(to right, lightpink, lightcoral);
}

.footer a {
  text-decoration: none;
  color: black;
  padding: 10px;
}

.footer a:hover {
  color: aliceblue;
  border-bottom: 1px solid aliceblue;
}

/* page not found css */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: solid palevioletred;
  border: 1px solid palevioletred;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}

.pnf-btn:hover {
  background-color: rgb(252, 96, 148);
  -webkit-text-fill-color: white;
}

/* pages css */
.col-md-6 {
  padding: 80px;
}

.col-md-4 {
  padding: 80px;
}

/* Register Form css */
.form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 70vh;
  background-color: aliceblue;
  background-image: linear-gradient(0deg, #ffdee9 0%, #62a5a3 100%);
}

.form-container form {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 15px;
  background-color: #fff;
}

.form-container form .title {
  text-align: center;
  margin-bottom: 15px;
  font-weight: bold;
  font-family: "Playfair Display", serif;
}

.form-container form input {
  border: none;
  border-bottom: 1px solid black;
  border-radius: 0;
}

.form-container form button {
  border: 1px solid black;
  border-radius: 0;
  background-color: rgb(252, 96, 148);
  color: white;
  width: 250px;
}

.form-container form button:hover {
  border: 1px solid black;
  background: black;
  background: -webkit-linear-gradient(to right, #434343, #000000);
  background: linear-gradient(to right, #434343, #000000);
}

.product-link {
  text-decoration: none !important;
  color: black !important;
}

/* dashboard */
.dashboard-body {
  background-color: #b5fffc;
}
